/* Header */
*{
  margin: 0px;
  padding: 0px ;
  /* color: rgb(36, 40, 48); */
  font-family: Roboto,Arial,Helvetica,sans-serif;
  /* width: 100% !important; */
}
.header{
  height: fit-content;
  background-color: whitesmoke;
  display: flex;
  /* padding: 5px; */
  align-items: center;
  /* padding: 12px 10px; */
 
}
.HeadContainer{
  /* margin: auto 0px; */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  /* border: 1px solid black; */
}
.SearchBox{
  display: flex ;
  /* justify-content: space-between; */
  align-items: center;
  width: 80%;
  /* border: 1px solid black; */
  
}
.logo{
  width: 38px;
  margin-right: 20px;
}

.search{
  /* border: 1px solid red; */
  height: 45px;
  width: fit-content !important ;
  display: flex;
  align-items: center;
  border-radius: 4px;
}

.input-group{
  width: 100% !important;
}
.S1 > .input-group-text{
  background-color: white !important;
}

.form-inline{
  width: 100%;
}
.S2 > .input-group-text{
  background-color: rgb(36, 40, 48);
  color: #fff;
}

h5{
  color: rgb(36, 40, 48);
}

input{
  height: 100%;
  width: auto;
  border: 0px;
  margin: 0px !important;
  padding: 10px;
  
}
.search2{
  padding-left: 0px !important; 
  padding-right: 0px !important;
  margin-left: 20px;

  
}
button{
  height: 100%;
  border: 0px;
  background-color: white;
  
}
.Sell > h6{
  display: inline;
  align-items: center;
  
  
}
.Sell{
  /* width: 200px ; */
  border-radius: 40px;
  width: 70px;
  border: 2px solid;
  padding: 0px;
}
.loginBox{
  display: flex;
  align-items: center;
  justify-content: center;
  
  margin-left: 20px;
  /* border: 1px solid; */
  padding: 0px !important;
  /* width: 400px !important; */
}
a > h5{
  display: inline;
  font-size: 16px;
  margin-right: 3px;
}
a:hover{
  color:  rgb(36, 40, 48) !important;
}
/* Header CSS End */

