  padding: 0px !important;
}

.btn-AllCate > b{
  margin-right: 7px;
  font-size: 13px;
}
.Nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 25px;
  /* border: 1px solid black; */
  
}
li{
  font-size: 14px;
  
}
a{
  color:rgb(36, 40, 48) !important ;
}
.catBox, .btn-AllCate{
  /* width: 100px !important; */
